import React, { useState, useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import logo from "./logo3.png"; // Import your logo here
import "@fontsource/amatic-sc/700.css";
function HomePage() {
  const [isMuted, setIsMuted] = useState(true);
  const videoRef = useRef(null);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.load(); // This ensures video is loaded on component mount
    }
  }, []);
  const handleVideoPlay = () => {
    videoRef.current.play();

    if (window.innerWidth > 768) {
      // Checks if the device width is greater than 768px, typical breakpoint for desktop
      setIsMuted(false);
      videoRef.current.muted = false;
    }
  };

  const handleVideoPause = () => {
    videoRef.current.pause();

    if (window.innerWidth > 768) {
      setIsMuted(true);
      videoRef.current.muted = true;
    }
  };
  return (
    <div style={styles.container}>
      <img src={logo} alt="Logo" style={styles.topLogo} />
      <h1 style={styles.title}>Добро пожаловать!</h1>
      <div
        style={styles.videoContainer}
        onMouseEnter={handleVideoPlay}
        onMouseLeave={handleVideoPause}
      >
        <video
          id="videoElement"
          style={styles.video}
          ref={videoRef}
          muted={isMuted}
          autoPlay
        >
          <source src="/assets/sampleVideo.mp4" type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>
      <Link to="/second">
        <button style={styles.button}>Начать</button>
      </Link>
      <div style={styles.navbar}>
        <img src={logo} alt="Logo" style={styles.bottomLogo} />
      </div>
    </div>
  );
}

const styles = {
  container: {
    fontFamily: '"Amatic SC", Helvetica',
    fontWeight: 700,
    color: "#402750",
    background: "linear-gradient(15deg, #D2B8E3 50%, transparent 50%)", // Adjusted gradient
    minHeight: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  },
  title: {
    textAlign: "center"
  },
  videoContainer: {
    border: "5px solid #402750",
    borderRadius: "15px",
    width: "300px",
    height: "300px",
    margin: "20px auto",
    cursor: "pointer",
    overflow: "hidden" // This ensures the video doesn't spill outside the container
  },
  video: {
    width: "100%",
    height: "100%",
    objectFit: "cover"
  },
  button: {
    backgroundColor: "#4bb150",
    fontFamily: '"Amatic SC", Helvetica',
    fontWeight: 700,
    color: "#402750",
    borderRadius: "20px",
    border: "none",
    padding: "10px 20px",
    cursor: "pointer"
  },
  topLogo: {
    position: "absolute",
    top: "10px",
    left: "10px",
    width: "50px" // or whatever size you want
  },
  navbar: {
    position: "fixed",
    bottom: "0",
    width: "100%",
    backgroundColor: "white",
    display: "flex",
    justifyContent: "space-between",
    padding: "10px 20px"
  },
  bottomLogo: {
    width: "50px" // or whatever size you want
  }
};

export default HomePage;
