import React, { useState, useRef, useEffect } from "react";
import logo from "./logo3.png";
import "@fontsource/amatic-sc/700.css";

function FourthPage() {
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [isMuted, setIsMuted] = useState(true);
  const videoRef = useRef(null);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.load(); // This ensures video is loaded on component mount
    }
  }, []);

  const handleEmailSend = (e) => {
    e.preventDefault();
    // Call your Flask backend API to send the video link to the provided email
    // Note: You'll need to create this API on the backend.
    fetch("/send-video-link", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ email: email })
    })
      .then((response) => response.json())
      .then((data) => {
        setMessage(data.message);
      })
      .catch((error) => {
        setMessage("Error sending email. Please try again.");
      });
  };
  const handleVideoPlay = () => {
    videoRef.current.play();

    if (window.innerWidth > 768) {
      // Checks if the device width is greater than 768px, typical breakpoint for desktop
      setIsMuted(false);
      videoRef.current.muted = false;
    }
  };

  const handleVideoPause = () => {
    videoRef.current.pause();

    if (window.innerWidth > 768) {
      setIsMuted(true);
      videoRef.current.muted = true;
    }
  };
  return (
    <div style={styles.container}>
      <img src={logo} alt="Logo" style={styles.topLogo} />
      <h1 style={styles.title}>Ваше видео!</h1>
      <div
        style={styles.videoContainer}
        onMouseEnter={handleVideoPlay}
        onMouseLeave={handleVideoPause}
      >
        <video
          id="videoElement"
          style={styles.video}
          ref={videoRef}
          muted={isMuted}
          autoPlay
        >
          <source src="/assets/sampleVideo.mp4" type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>
      <a
        href="/assets/sampleVideo.mp4"
        download="sampleVideo.mp4"
        style={{ margin: "20px" }}
      >
        <button style={styles.button}>Скачать</button>
      </a>
      <div style={{ margin: "20px" }}>
        <input
          type="email"
          placeholder="Введите свой мейл"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          style={styles.emailInput}
        />
        <button onClick={handleEmailSend} style={styles.button}>
          Отправить на мейл
        </button>
        {message && <p>{message}</p>}
      </div>
      <div style={styles.navbar}>
        <img src={logo} alt="Logo" style={styles.bottomLogo} />
      </div>
    </div>
  );
}

const styles = {
  container: {
    fontFamily: '"Amatic SC", Helvetica',
    fontWeight: 700,
    color: "#402750",
    background: "linear-gradient(15deg, #D2B8E3 50%, transparent 50%)",
    minHeight: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  },
  title: {
    textAlign: "center",
    marginBottom: "20px"
  },
  videoContainer: {
    border: "5px solid #402750",
    borderRadius: "15px",
    width: "300px",
    height: "300px",
    margin: "20px auto",
    cursor: "pointer",
    overflow: "hidden"
  },
  video: {
    width: "100%",
    height: "100%",
    objectFit: "cover"
  },
  button: {
    backgroundColor: "#4bb150",
    fontFamily: '"Amatic SC", Helvetica',
    fontWeight: 700,
    color: "#402750",
    borderRadius: "20px",
    border: "none",
    padding: "10px 20px",
    cursor: "pointer",
    margin: "5px"
  },
  emailInput: {
    padding: "10px",
    fontFamily: '"Amatic SC", Helvetica',
    fontWeight: 700,
    margin: "5px",
    borderRadius: "10px"
  },
  topLogo: {
    position: "absolute",
    top: "10px",
    left: "10px",
    width: "50px"
  },
  navbar: {
    position: "fixed",
    bottom: "0",
    width: "100%",
    backgroundColor: "white",
    display: "flex",
    justifyContent: "space-between",
    padding: "10px 20px"
  },
  bottomLogo: {
    width: "50px"
  }
};

export default FourthPage;
