import { Link } from "react-router-dom";
import logo from "./logo3.png";
import React, { useState, useRef, useEffect } from "react";

const ThirdPage = ({ history }) => {
  const [isMuted, setIsMuted] = useState(true);
  const videoRef = useRef(null);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.load(); // This ensures video is loaded on component mount

      // Add event listener for time update
      videoRef.current.addEventListener("timeupdate", handleTimeUpdate);
    }

    // Clean up the event listener when component is unmounted
    return () => {
      if (videoRef.current) {
        videoRef.current.removeEventListener("timeupdate", handleTimeUpdate);
      }
    };
  }, []);

  const handleTimeUpdate = () => {
    if (videoRef.current.currentTime >= 3) {
      videoRef.current.currentTime = 0; // Reset video time to start
      videoRef.current.play();
    }
  };
  const handleProceed = () => {
    window.location.replace("https://buy.stripe.com/test_7sIcOi4533wOdQ4bII");
  };

  const handleVideoPlay = () => {
    videoRef.current.play();

    if (window.innerWidth > 768) {
      // Checks if the device width is greater than 768px, typical breakpoint for desktop
      setIsMuted(false);
      videoRef.current.muted = false;
    }
  };

  const handleVideoPause = () => {
    videoRef.current.pause();

    if (window.innerWidth > 768) {
      setIsMuted(true);
      videoRef.current.muted = true;
    }
  };

  return (
    <div style={styles.container}>
      <img src={logo} alt="Logo" style={styles.topLogo} />
      <h1 style={styles.title}>Мы уже у цели! Вам нравится?</h1>
      <div
        style={styles.uploadBox}
        onMouseEnter={handleVideoPlay}
        onMouseLeave={handleVideoPause}
      >
        <video
          id="videoElement"
          style={styles.uploadedImage}
          ref={videoRef}
          muted={isMuted}
          autoPlay
        >
          <source src="/assets/sampleVideo.mp4" type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>

      <button onClick={handleProceed} style={styles.button}>
        Хочу полную версию!
      </button>
      <div style={styles.backButtonContainer}>
        <button onClick={() => history.push("/second")} style={styles.button}>
          Назад
        </button>
      </div>
      <div style={styles.navbar}>
        <img src={logo} alt="Logo" style={styles.bottomLogo} />
      </div>
    </div>
  );
};

